@charset "UTF-8";

body {
	position: relative;
	font-family: $body-font;
	font-size: 1rem;
	font-weight: 500;
	line-height: 1.5;
	color: $body-font-color;
	word-break: break-word;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
	margin-bottom: 0.5rem;
	font-family: $title-font;
	line-height: 1.1;
	color: $title-font-color;
}

h1, .h1 {
	font-size: 2rem;
	font-weight: 700;
}
  
h2, .h2 {
	font-size: 1.5rem;
	font-weight: 700;
}

h3, .h3 {
	font-size: 1.35rem;
	font-weight: 400;
}

h4, .h4 {
	font-size: 1.2rem;
	font-weight: 400;
}

h5, .h5 {
	font-size: 1rem;
	font-weight: 400;
}

h6, .h6 {
	font-size: 0.85rem;
	font-weight: 400;
}

a {
	color: $link-color;
	text-decoration: none;
	transition: all 0.5s ease;

	&:hover {
		color: $link-color-hover;
		text-decoration: none;
	}
	&:active {
		color: $link-color-active;
	}
	&:focus { outline: none; }
}

b, strong {
    font-weight: 700;
}

.highlighted {
	color: $pr-color;
}

.message {
	font-size: 1.25rem;
    text-align: center;
    margin-bottom: 25px;

    &.error {
    	color: $danger-color;
    }
}

@media (min-width: 768px) {
	h1, .h1 {
		font-size: 3.2rem;
	}
}